@import "common";
.page_solution {
  * {
 }    // border: solid 1px

  section {
    min-height: 0px;
    position: relative;
    .section_title {
      margin-top: 0;
      margin-bottom: $gutter_width*2; }
    .container {
      .col_left,.col_right {
        align-items: flex-start;
        text-align: left; } } }

  .section_hero {
    min-height: 50vh;
    @include flex_center;
    background-size: cover;
    background-position: center center;
    padding-top: grid_sz(0,13);
    padding-bottom: grid_sz(0,7);
    .section_title {

      text-align: center;
      opacity: 0.8;
      letter-spacing: 10px; }
    &,h1 {} }
  .block_50_percent {
    @include size(50%,100%);
    display: inline-block;
    float: left;


    &[data-image="1"] {
      background-image: url("../img/homepage/Solution2.jpg"); }
    &[data-image="2"] {
      background-image: url("../img/homepage/Post2.jpg"); }
    &[data-image="3"] {
      background-image: url("../img/homepage/Post3.jpg"); }
    &[data-image="4"] {
      background-image: url("../img/homepage/Post1.jpg"); } }
  .bg_color_split {
    font-size: 0;
    padding: 0; }


  h1.section_title {
    margin-top: $gutter_width*1.5;
    line-height: 1.4em;
    padding-bottom: 0px;
 }    // margin-bottom: $gutter_width*1.5

  .section_solution_1 {
    padding-top: grid_sz(0,2);
    padding-bottom: grid_sz(0,4); }

  .section_solution_2 {
    padding-top: grid_sz(0,2);
    padding-bottom: grid_sz(0,2); }

  .section_solution_1,.section_solution_2 {

    h3 {
      color: $color_theme;
      font-weight: 300; }

    hr {
      margin-top: $gutter_width*1.5;
      width: $gutter_width*2;
      border-color: $color_theme;
      margin-left: 0;
      margin-bottom: $gutter_width; } }

  .section_solution_1 {
    display: flex;
    position: relative;

    &,p,h4 {
      color: white;
      font-weight: 300; } }

  .section_solution_2 {
    .container {
      align-items: flex-start; }
    &:before {
      @include cover_opacity(rgba(white,0.7)); }
    .area_env {
      min-height: grid_sz(0,13);
      margin-bottom: grid_sz(0,2); }

    .area_type {
      min-height: grid_sz(0,10);
      margin-bottom: 0; } }


  .envtext {
    font-weight: 400;
    margin-bottom: $gutter_width*3;
    color: $color_grey; }

  .section_talk {
    height: $gutter_width*18;
    background-image: url("../img/homepage/Solution3.jpg");
    background-size: 100% auto;
    * {
      color: white; }
    @include flex_center;
    .container {
      position: relative; }
    .lr_btn {
      color: $color_white;
      cursor: pointer;
      z-index: 100;

      @include ab_center; }
    h2 {
      margin-top: grid_sz(0,6);
      font-weight: 300;
      font-size: 26px; }
    h4 {
      margin-top: $gutter_width*3;
      font-weight: 300; }
    .btn_pre {
      left: 30px; }
    .btn_post {
      left: initial;
      right: 30px; }

    .talk_box {
      position: relative; }
    .slick {
      height: 100%;
      width: grid_sz(8,7); }
    h4 {
      text-align: right; } } }

