@import "_variables";

//reset

html, body, div, span, applet, object, iframe,h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,b, u, i, center,
dl, dt, dd, ol, ul, li,fieldset, form, label, legend,table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  // font: inherit
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

//----



//----

* {
  font-family: " L" , 'Noto Sans TC','';
  background-size: cover;
  background-position: center center;
  box-sizing: border-box;
  position: relative;
  vertical-align: top;
  background-repeat: no-repeat;
  font-weight: 400;


 }  // font-family: ' R'


//slick

.slick-track,.slick-list,.slick-slider {
  height: 100%; }

.slick-dotted.slick-slider {
  margin: 0; }

.slick-slide:focus {
  outline: none; }

.fa.fa-angle-left,.fa.fa-angle-right {
  @include ab_center;
  z-index: 105;
  color: white;
  font-size: grid_sz(0,1.2);
  top: calc(50% - #{grid_sz(0,1)/2});
  cursor: pointer; }
.fa.fa-angle-left {
  left: 50px; }
.fa.fa-angle-right {
  left: initial;
  right: 50px; }


ul.slick-dots {
  bottom: 20px;
  li {
    margin: 0;
    padding: 0px 13px;
    button {
      &:before {
        @include trans;
        content: "";
        @include size(8px);
        border-radius: 50%;
        background-color: rgba(white,0.8);
        border: solid 1px rgba(white,1); } } } }


//tag size
h1 {
  @include paragraph_size_leading(48px,83px,2px); }
h2 {
  @include paragraph_size_leading(34px,50px,2px); }
h3 {
  @include paragraph_size_leading(26px,36px,2px); }
h4 {
  @include paragraph_size_leading(21px,31px,2px); }
h5 {
  @include paragraph_size_leading(17px,28px,2px); }
h6 {
  @include paragraph_size_leading(16px,27px,2px); }
p {
  @include paragraph_size_leading(16px,27px,3px);
  color: $color_grey;
  text-align: left !important; }



hr {
  margin-top: grid_sz(0,2);
  margin-bottom: grid_sz(0,2);
  width: 100%;
  border: none;
  border-bottom: solid 1px #eee; }


body {
  @include paragraph_size_leading(16px,27px,3px);
  font-weight: 400; }

h1,h2,h3,h4,h5,h6 {
  color: $color_grey_dark;
  font-weight: 300;
 }  // min-width: 1600px

.color_theme {
  color: $color_theme; }

.color_white {
  color: #fff; }


.timeline {
  width: 100%;
  height: 2px;
  background-color: #ddd;
  position: absolute;
  bottom: 0px;
  z-index: 90;
  .value {
    width: 50%;
    height: 100%;
    background-color: #888; } }

//----


section {
  // min-height: 100vh
  background-size: cover;
  background-position: 60% center;
  .section_title {
    margin-bottom: grid_sz(0,3);
    margin-top: grid_sz(0,-3);
    @include trans;
    &.initial {
      opacity: 0; } }
  .section_para {
    @include trans(1s,0.2s);
    &.initial {
      opacity: 0; } }

  .slide_bg_set {
    width: 100%;
    height: 100%; } }

//---

.container {
  max-width: grid_sz(12,13);
  margin: auto;
  width: 100%;
  height: 100%;


  &.flex {
    display: flex;
    flex-direction: row;
    align-items: center; }

  &.center {
    justify-content: center; }

  &.wrap {
    flex-wrap: wrap; }

  &.full {
    max-width: initial; }

  &.column {
    flex-direction: column; }

  &.row {
    flex-direction: row; }

  .col_left,.col_right {
    width: 50%;
    padding: grid_sz(0,1,10px);
    text-align: center;
    height: 100%;
    @include flex_center;
    flex-direction: column;
    p {
      text-align: left; } }

  @include range_width(0px,700px) {
    &.flex {
      flex-direction: column; }
    .col_left,.col_right {
      width: 100%;
      min-height: 50vh; } }
  .col_hero {
    width: grid_sz(6,5);
    text-align: center; } }

.bg_parallax {
  background-size: auto 100%; }
.bg_parallax_about {
  background-size: 100% auto; }
.cover_image {
  background-color: $color_theme; }

.btn {
  border-radius: 2px;
  letter-spacing: 2px;
  padding: 10px grid_sz(0,1);
  font-weight: 300;
  font-size: 15px;
  letter-spacing: 4px;
  text-decoration: none !important;
  cursor: pointer;
  @include trans; }

.btn-primary {
  border: none;
  background-color: $color_theme;
  color: $color_white;
  &:hover {
    background-color: lighten($color_theme,15);
    color: $color_white; } }

.btn-primary-lighter {
  border: none;
  background-color: lighten($color_theme,7);
  color: $color_white;
  &:hover {
    background-color:  lighten($color_theme,15);
    color: $color_white;
    border-color:  lighten($color_theme,7); } }

.btn-transparent {
  background-color: transparent;
  border: solid 1px $color_white;
  color: $color_white;
  &:hover {
    background-color: $color_white;
    color: $color_theme;
    border-color: $color_white; } }

@mixin cover_opacity($color: black) {
  display: block;
  content: "";
  position: absolute;
  @include size(100%,100%);
  left: 0;
  top: 0;
  background-color: $color; }

//----------------

//
.nav_line_split {
  list-style: none;
  padding: 0;
  li {
    display: inline-block;
    padding: 0px $gutter_width;
    // margin-bottom: $gutter_width*2
    border-right: solid 1px $color_theme;
    cursor: pointer;
    @include trans;
    &:hover {
      color: $color_theme; }
    &:last-child {
      border-right: none; }
    &.active {
      color: $color_theme; } } }

a {
  color: $color_theme; }

.default_bg {
  background: url("../img/homepage/Home7.jpg") no-repeat 60% bottom;
  &:before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background-color: rgba(white,0.4); } }


//nav
//--------------------------

nav {
  $nav_height: grid_sz(0,3);
  @include size(100%,$nav_height);

  position: fixed;
  top: 0;
  font-size: 17px;
  @include colors_bg_text(white,$color_grey_dark);
  padding-top: grid_sz(0,1)/2;
  border-top: solid 3px $color_theme;
  z-index: 200;
  @include trans;

  * {
    color: $color_grey;
    text-decoration: none;
    font-weight: 400; }

  .row {
    display: flex;
    width: 100%;
    justify-content: space-between; }

  //---
  .navbar-header {
    width: grid_sz(3,2);
    img {
      height: 50px;
      @include trans; } }

  .nav-leftpart {
    > * {
      display: inline-block;
      vertical-align: top; } }

  .navbar-left {
    margin-left: grid_sz(0,1);
    width: grid_sz(7,6); }

  .navbar-right {
    width: grid_sz(2,2);
    display: flex;
    justify-content: flex-end;
    align-items: space-between; }

  .navbar-left,.navbar-right {
    margin-top: grid_sz(0,1)/2;
    @include trans; }

  //---



  .navbar-nav li {
    list-style: none;
    display: inline-block;
    margin-right: grid_sz(0,1);

    & a.router-link-active {
      @include colors_bg_text(transparent,$color_theme);
      border-bottom: solid 1px $color_theme;
      &:hover {
        color: $color_theme;
        background-color: transparent; } }

    & > a {
      padding-bottom: 6px;
      border-bottom: solid 1px transparent;
      @include trans; }


    & ul.subnav {
      width: 100vw;
      position: fixed;
      @include pos(0,$nav_height);
      opacity: 0;
      overflow: hidden;
      max-height: 0px;
      height: 0px;
      transition: 0.5s 0.2s, top 0.5s 0s;
      text-align: center;
      background-color: rgba($color_theme,0.9);
      li {
        margin: 0;
        &:nth-child(2n) {
          margin-left: grid_sz(0,1);
          margin-right: grid_sz(0,1); } }
      .container {
        justify-content: flex-start; }
      li a {
        color: $color_white; } }

    &:hover {
      a {
        border-bottom: solid 1px $color_theme; }
      .subnav {
        max-height: 200px;
        height: grid_sz(0,3);
        opacity: 1;
        z-index: 100;
        padding: grid_sz(0,1) 0px; } } }

  @include range_width(0px,1080px) {
    .navbar-nav {
      .func_lang,.func_search {
        display: none; } }
    .row {
      justify-content: space-between; }
    .nav-right {
      justify-content: flex-end; } }


  @include range_width(0px,970px) {
    .navbar-nav.navbar-left {
      display: none; } }

  //---- when at top of the page
  &.at_top {
    $nav_height_at_top: grid_sz(0,5);
    height: $nav_height_at_top;
    padding: grid_sz(0,1);

    .navbar-header {
      width: grid_sz(3,2);
      img {
        height: 65px; } }

    .navbar-left,.navbar-right {
      margin-top: grid_sz(0,1); }


    .navbar-nav li {
      & ul.subnav {
        @include pos(0,$nav_height_at_top); } } } }


.func_size {
  padding-top: 3px;
  .icon_big,.icon_small {
    cursor: pointer; }
  .icon_big {
    display: none; }
  &.bigger {
    .icon_big {
      display: block; }
    .icon_small {
      display: none; } } }


.slide_bullet {
  list-style: none;
  position: fixed;
  right: 40px;
  top: 50%;
  transform: translate(-50%,0px);
  z-index: 10;
  li {
    display: block;
    @include size(9px);
    border-color: $color_grey_light;
    border-radius: 50%;
    margin-bottom: 16px;
    cursor: pointer;
    @include trans;
    position: relative;
    &:after {
      content: "";
      display: block;
      border-radius: 50%;
      @include ab_center;
      @include size(6px);
      background-color: darken($color_grey,9);
      @include trans; }

    &:hover {
      border-color: $color_theme;
      &:after {
        background-color: $color_theme;
        @include size(12px); } }
    &.active {
      background-color: $color_theme;
      &:after {
        background-color: $color_theme;
        @include size(12px); } } } }


//----------------------------------------------
footer {
  background-color: $color_theme;
  color: $color_white;
  height: 61px;
  font-weight: 300;

  .info {
    display: inline-block;
    margin: 0;
    font-size: 14px;
    opacity: 0.9;
    letter-spacing: 2px;
    width: grid_sz(6,5); }

  ul.footer_nav {
    display: block;
    li {
      display: inline-block;
      letter-spacing: 3px;

      padding: 0px 15px;
      border-right: solid 1px $color_white;
      @include trans;
      &:hover a {
        color: $color_white; }
      &:last-child {
        border-right: none; } }

    a {
      color: rgba($color_white,0.7);
      text-decoration: none;
      font-size: 17px; } }


  @include range_width(0px,930px) {
    height: auto; }

  .container.flex {
    @include flex_split("lr");
    align-items: center;



    @include range_width(0px,930px) {
      flex-direction: column;
      padding: grid_sz(0,1) 0px;
      .info {
        margin-bottom: grid_sz(0,1); } }

    @include range_width(0px,600px) {
      ul.footer_nav {
        display: none; }
      .info {
        margin-bottom: 0px; } } } }




//-----------------------------------------------
section.section_solution {
  background: url("../img/homepage/Home7.jpg") 60% bottom;
  min-height: 100vh;
  position: relative;
  padding-top: grid_sz(0,2);
  padding-bottom: grid_sz(0,2);
  text-align: center;
  .container.flex {
    height: grid_sz(0,24);
    min-height: initial;
    padding: 0px grid_sz(0,1);
    justify-content: center; }
  .slogan {
    margin-top: grid_sz(0,1);
    margin-bottom: $gutter_width;
    color: $color_grey_light;
    font-weight: 400; }
  &:before {
    @include cover_opacity(rgba(white,0.6)); }
  h3.section_title {
    margin-top: grid_sz(0,2);
    margin-bottom: grid_sz(0,2);
    color: $color_grey_dark;
    font-size: 24px; }
  .solution_box_row {
    margin-top: $gutter_width*1;
    // margin-bottom: $gutter_width*3
    min-height: 500px; }

  .solution_box {
    text-align: center;
    width: grid_sz(4,3);
    cursor: pointer;
    @include trans;

    &:nth-child(2n) {
      margin-left: $gutter_width;
      margin-right: $gutter_width; }

    & .box_inner {
      display: block;
      position: relative;
      @include box_shadow;
      @include trans;
      height: grid_sz(0,19);
      overflow: hidden;
      cursor: pointer;

      .box_info {
        margin-top: grid_sz(0,3); }
      .box_btn {
        margin-bottom: grid_sz(0,3); }

      .box_text {
        @include flex_split("td");
        @include ab_full;
        z-index: 10; }

      &:after,&:before {
        @include pseudo_ab_full; }

      &:after {
        background-color: rgba(0,0,0,0.4);
        // opacity: 0
        @include trans; }

      &:hover {
        height: grid_sz(0,24);
        background-color: transparent;
        color: #333;

        .box_info {
          h4.solution_title {
            color: $color_grey_dark;
            &:after {
              border-bottom: solid 2px  $color_grey_dark; } } }
        .btn {
          opacity: 1; }

        &:after {
          opacity: 0; } }

      h4 {
        color: $color_white; }

      h4.solution_title {
        line-height: 2em;
        color: $color_white;
        @include trans;
        font-weight: 400;

        &:after {
          content: "";
          display: block;
          width: grid_sz(1,0);
          margin: auto;
          margin-top: grid_sz(0,2);
          @include trans;
          border-bottom: solid 2px $color_white; } } }




    .btn {
      margin-bottom: $gutter_width*2;
      @include trans;
      opacity: 0;
      cursor: pointer; }

    &:nth-child(1) .box_inner:before {
      background-image: url("../img/homepage/Home8-1.jpg");
      background-position: 60% bottom;
      background-size: 105% auto; }


    &:nth-child(2) .box_inner:before {
      background-image: url("../img/homepage/Home8-2.jpg");
      background-position: 60% bottom;
      background-size: 105% auto; }


    &:nth-child(3) .box_inner:before {
      background-image: url("../img/homepage/Home8-3.jpg");
      background-position: 60% bottom;
      background-size: 105% auto; } }


  .row_else {
    padding: $gutter_width*1;
    padding-bottom: $gutter_width*2; } }


//----------------------------------------------
.detail_footer {
  min-height: initial;
  padding: $gutter_width*3 0px;
  padding-bottom: $gutter_width*2;

  .container {
    align-items: flex-start;
    @include range_width(0px,768px) {
      flex-direction: column; } }

  .col_address {
    width: grid_sz(4,3); }

  .col_question {
    width: grid_sz(5,4);
    margin-left: grid_sz(0,1);
    margin-right: grid_sz(0,1); }
  .col_corp {
    width: grid_sz(3,2);

    .item {}
    .company_icon {
      width: grid_sz(1,1);
      margin: grid_sz(0.1);
      margin-left: auto;
      margin-right: auto; } }


  .col_address,.col_question,.col_corp {
    color: $color_grey_light; }

  h5 {
    font-size: 18px;
    color: $color_grey_dark;
    height: grid_sz(0,2); }

  //--------

  hr {
    border: none;
    border: solid 1px #ddd;
    margin-top: grid_sz(0,1); }

  .social_icon {
    width: 30px;
    margin-right: 10px;
    vertical-align: middle;
    &.fb {
      width: 20px; } }
  .more {
    border-bottom: solid 2px $color_theme;
    padding-bottom: 3px;
    cursor: pointer;
    &:hover {
      color: $color_theme;
      text-decoration: none; } }


  .icon {
    @include size(18px);
    // background-color: darken($color_white,15)
    border-radius: 2px;
    position: relative;
    display: inline-block;
    position: absolute;
    left: -26px;
    top: 5px;
    opacity: 0.7;
    cursor: pointer;
    @include trans;

    &:hover {
      background-color: $color_theme;
      &:before,&:after {
        background-color: $color_white; } }

    &:before,&:after {
      // transform-origin: center center
      @include trans;
      display: block;
      content: "";
      width: 10px;
      height: 2px;
      background-color: $color_theme;

      @include ab_center; }
    &.icon_plus {

      &:before {
        transform: translate(-50%,-50%) rotate(-90deg); }
      &:after {
        transform: translate(-50%,-50%) rotate(180deg); } } }

  .col_address {
    p {
      font-size: 16px; }
    hr {
      border-bottom: solid 1px #aaa; } }
  .question_list {
    position: relative;

    & {
      list-style: none;
      padding-left: $gutter_width*1.5; }

    li {
      margin-bottom: $gutter_width;
      position: relative;

      &.open {
        .answer {
          max-height: 150px;
          margin-top: $gutter_width/2; } } }

    .question {
      color: $color_grey_light;
      font-size: 16px;
      font-weight: 400;
      letter-spacing: 1px;
      cursor: pointer; }
    .answer {
      color: $color_theme;
      font-weight: 400;
      margin-top: 0;
      max-height: 0px;
      overflow: hidden;
      @include trans;

      // max-height: 0px
 }      // overflow: hidden

    input[type=checkbox]:checked+.answer {
      .answer {
        display: block;
        max-height: 50px; } } }

  .company_icon {
    width: 33%;
    display: block;
    margin-bottom: 10px;
    margin-left: auto;
    margin-right: auto; }


  .go_to_topbtn {
    position: fixed;
    background-color: $color_theme;
    @include size(40px,40px);
    cursor: pointer;

    border-radius: 50%;
    bottom: 65px;
    right: 65px;

    i {
      @include ab_center;
      color: $color_white;
      font-size: 25px; } } }


//-------------------------
.fade-enter-active,.fade-leave-active {
  transition: opacity .5s; }

.fade-enter,.fade-leave-to {
  opacity: 0; }
.fade-enter-to,.fade-leave {
  opacity: 1; }


